export default {
	install(Vue) {
		Vue.mixin({
			data() {
				return {
					phoneReg:/^1[3-9]\d{9}$/,
					formRules: {
						loginName: {
							rules: [{
								required: true,
								errorMessage: ''
							}]
						},
						password: {
							rules: [{
								required: true,
								errorMessage: ''
							}]
						},
						phone: {
							rules: [
								{
								required: true,
								errorMessage: ''
							},
							{
								validateFunction:this.validatePhone
							}
							]
						},
						vCode: {
							rules: [
								{
								required: true,
								errorMessage: ''
							},
							{
								validateFunction:this.validateCode
							}
							]
						}
					}
				}
			},
			methods: {
				validatePhone(rule,val,data,callback) {
					if(this.phoneReg.test(val)){
						callback();
					}else{
						callback('')
					}
				},
				validateCode(rule,val,data,callback) {
					if(val === this.phoneCode){
						callback();
					}else{
						callback('')
					}
				},
			},
		})
	}
}
